import React from "react";
import office from "../../../assets/image/4.png";
export default function ChoseUs() {
  return (
    <>
      <div className="chose-us py-5 overflow-hidden">
        <div className="container">
          <div className="row">
            <div className="col-md-6">
              <img src={office} className="img-fluid" alt="" />
            </div>
            <div className="col-md-6">
              <div className="chose-use-content">
                <div className="chose-us__title">
                  <h3 className="title">What Sets Us Apart ?</h3>
                </div>
                <div className="chose-us__sub-title" data-aos="fade-right">
                  <span className="sub-title">
                    Designed for startups with expert developer
                  </span>
                </div>
                <div className="row align-items-start pt-4">
                  <div className="col-1">
                    <span className="checked-icon">
                      <i className="fa-solid fa-check"></i>
                    </span>
                  </div>
                  <div className="col-11">
                    <h5 className="desc-title" data-aos="fade-left">
                      Privacy First{" "}
                    </h5>
                    <span className="desc-subtitle">
                      Qchat stands out as the only solution that enables
                      communication through QR code scanning without{" "}
                    </span>
                  </div>
                </div>
                <div className="row align-items-start pt-4">
                  <div className="col-1">
                    <span className="checked-icon">
                      <i className="fa-solid fa-check"></i>
                    </span>
                  </div>
                  <div className="col-11">
                    <h5 className="desc-title" data-aos="fade-left">
                      Versatility Unleashed{" "}
                    </h5>
                    <span className="desc-subtitle">
                      Qchat isn't just for chatting. Stick Qchat codes on your
                      pets, valuables, luggage, or items for sale. It's a
                      multi-functional tool designed to simplify your life.{" "}
                    </span>
                  </div>
                </div>
              </div>

              {/* <button className="button mt-3">READ MORE</button> */}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
