import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import logo from "../../../assets/image/QChat-logo.png";

export default function Navbar() {
  const [navbar, setNavbar] = useState(false);

  useEffect(() => {
    window.addEventListener("scroll", () => {
      if (window.scrollY >= 170) {
        setNavbar(true);
      } else {
        setNavbar(false);
      }
    });
  }, []);

  return (
    <>
      {/* first Navbar */}
      <nav className="navbar navbar-expand-lg navbar-light ">
        <div className="container">
          <Link className="navbar-brand" to="/">
            <img src={logo} className="w-50" alt="" />
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0 text-center bg-md-dark">
              <li className="nav-item mx-3">
                <Link className="nav-link active" aria-current="page" to="/">
                  HOME
                </Link>
              </li>
              <li className="nav-item mx-3">
                <Link className="nav-link" to="/contact">
                  Contact Us
                </Link>
              </li>
              <li className="nav-item mx-3">
                <Link className="nav-link " to="/FAQ">
                  FAQ
                </Link>
              </li>
              <li className="nav-item mx-3">
                <Link className="nav-link ">Plans</Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>

      {/* second Navbar */}
      <nav
        className={
          navbar
            ? "navbar second navbar-expand-lg navbar-light move-to-bottom  "
            : "navbar second navbar-expand-lg navbar-light "
        }
      >
        <div className="container d-flex">
          <Link className="navbar-brand" href="#">
            <img src={logo} className="w-50" alt="" />
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0 text-center ">
              <li className="nav-item mx-3">
                <Link className="nav-link   active" aria-current="page" to="/">
                  HOME
                </Link>
              </li>
              <li className="nav-item mx-3">
                <Link className="nav-link  " to="contact">
                  Contact Us
                </Link>
              </li>
              <li className="nav-item mx-3">
                <Link className="nav-link  " href="#">
                  FAQ
                </Link>
              </li>
              <li className="nav-item mx-3">
                <Link className="nav-link  " href="#">
                  Subscriptions
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
}
