import React, { useEffect, useState } from "react";
import qchat from "../../../assets/image/test.png";
import loading from "../../../assets/image/preloader.gif";
import Navbar from "../Navbar/Navbar";
import Footer from "../Footer/Footer";

export default function ContactUs() {
  return (
    <>
      <Navbar />
      <div className="contact  d-flex justify-content-center align-items-center">
        <div className="container ">
          <div className="row justify-content-center align-items-center">
            <div className="col-md-6">
              <div className="contact__content text-center">
                <div className="contact-title ">
                  <h3 className="contact-title__content">Contact Us</h3>
                </div>
                <div className="contact-info">
                  <div className="contact-info__link">
                    <i className="fa-regular fa-envelope p-3"></i>
                    <span>contact@qchat.com</span>
                  </div>
                  <div className="contact-info__link">
                    <i className="fa-solid fa-phone p-3"></i>
                    <span>(02) 9876 5432</span>
                  </div>
                  <div className="contact-info__link">
                    <i className="fa-solid fa-location-dot p-3"></i>{" "}
                    <span>Australia,Canberra</span>
                  </div>
                  <div className="social" style={{ marginTop: 20 }}>
                    <ul className="list-inline">
                      <li className="list-inline-item">
                        <i className="fa-brands fa-facebook fs-1"></i>
                      </li>
                      <li className="list-inline-item">
                        <i className="fa-brands fa-tiktok fs-2 mx-3"></i>{" "}
                      </li>
                      <li className="list-inline-item">
                        <i className="fa-brands fa-linkedin fs-1"></i>{" "}
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>

            <div className="col-md-6">
              <div className="qr-title">
                <h3 className="qr-title__content">
                  You can scan the Qchat code and you will be in touch with us:
                </h3>
              </div>
              <div className="qr-img text-center">
                <img src={qchat} className="w-50" alt="" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
}
