import React, { useState, useEffect } from "react";
import ChoseUs from "../../Components/HomePageComponents/ChoseUs/ChoseUs";
import HomePageBanner from "../../Components/HomePageComponents/HomePageBanner/HomePageBanner";
import HowItWork from "../../Components/HomePageComponents/HowItWork/HowItWork";
import Navbar from "../../Components/HomePageComponents/Navbar/Navbar";
import OurStory from "../../Components/HomePageComponents/OurStory/OurStory";
import Services from "../../Components/HomePageComponents/Services/Services";
import Footer from "../../Components/HomePageComponents/Footer/Footer";
import loading from "../../assets/image/preloader.gif";
import AOS from "aos";
import "aos/dist/aos.css";
import ScrollToTop from "../../Components/ScrollToTop/ScrollToTop";
export default function Home() {
  const [isloading, setloading] = useState(false);
  useEffect(() => {
    AOS.init({
      duration: 2000,
    });
    AOS.refresh();
    setloading(true);
    setTimeout(() => {
      setloading(false);
    }, 1200);
  }, []);

  return (
    <>
      {isloading ? (
        <div className="preloader d-flex justify-content-center align-items-center vh-100 ">
          <img src={loading} alt="" />
        </div>
      ) : (
        <>
          <ScrollToTop />
          <Navbar />
          <HomePageBanner />
          <OurStory />
          <ChoseUs />
          <Services />
          <HowItWork />
          <Footer />
        </>
      )}
    </>
  );
}
