import React from "react";
import banner from "../../../assets/image/5.png";
export default function HomePageBanner() {
  return (
    <>
      <div className="home-banner overflow-hidden d-flex align-items-center">
        <div className="container">
          <div className="row justfy-contet-center align-items-center">
            <div className="col-md-7">
              <h3 className="home-banner__title" data-aos="fade-right">
                Welcome to Qchat
              </h3>
              <div className="home-banner__sub-title mb-5">
                <span className="sub-title">
                  an Australian-built and operated mobile application that is
                  reshaping the way people connect and communicate. At Qchat, we
                  believe in the power of communication without compromising
                  your privacy. Our mission is to provide a secure and versatile
                  platform that allows individuals to connect seamlessly without
                  the need to share personal details.
                </span>
              </div>
              {/* <div className="play-video">
                <span className="play-video__icon">
                  <i className="fa-solid fa-play"></i>
                </span>
              </div> */}
            </div>
            <div className="col-md-5 d-md-block d-none">
              <div className="home-banner__img">
                <img src={banner} className="w-100" alt="" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
