import React from "react";
import "./QnAs.module.scss";
import { Link } from "react-router-dom";

export default function QnAs() {
  return (
    <>
      <div className="container" style={{ padding: "20px 0" }}>
        <div className="accordion accordion-flush" id="accordionFlushExample">
          {[
            {
              q: "What is Qchat?",
              a: "Qchat is a mobile application designed to facilitate secure communication without the need to exchange personal details. Users can connect with each other by scanning QR codes, ensuring privacy and control over shared information.",
            },
            {
              q: "How do I get started with Qchat?",
              a: "Getting started with Qchat is easy! Simply download the app from the Play Store or Apple Store, complete the registration process, and enjoy a free trial period. Cancel anytime hassle-free.",
            },
            {
              q: "Is Qchat secure?",
              a: "Yes, security is our top priority. Qchat employs the latest encryption technologies to ensure the privacy and protection of user data. Your information is secure, and you have control over what you share.",
            },
            {
              q: "What makes Qchat unique?",
              a: (
                <>
                  {
                    "Qchat is the only platform that enables communication through QR code scanning without the exchange of personal details. It's versatile, allowing users to stick Qchat codes on pets, valuables, luggage, and items for sale. etc. "
                  }
                  <br />
                  {
                    " Uses are unlimited and if you have any other use in mind, just let us know on "
                  }
                  <a href="mailto:myinput@qchat.com.au">
                    {"myinput@qchat.com.au"}
                  </a>
                  {","}
                  <br />
                  {
                    "and we will do our best to facilitate it to you, if needed."
                  }
                </>
              ),
            },
            {
              q: 'What does "soft delivery" of QChat codes mean?',
              a: "Soft delivery refers to the convenient and instant access to your QChat codes as soon as you subscribe or start your free trial period. Once you're on board, you can retrieve your 2 or 3 codes (based on your subscription) through your account on the QChat application. These codes are digitally available for you to print or save on various devices such as your computer, tablet, mobile, or laptop.",
            },
            {
              q: "Are there alternative ways to receive QChat codes besides soft delivery?",
              a: (
                <>
                  {
                    "Yes, we offer alternative forms of QChat code delivery to cater to various needs. These include:"
                  }
                  <ul>
                    <li>
                      <b>{"Metallic Medal:"}</b>{" "}
                      {"Ideal for attaching QChat codes to your pet's collar."}
                    </li>
                    <li>
                      <b>{"Stickers:"}</b>{" "}
                      {"Ideal for attaching QChat codes to your pet's collar."}
                    </li>
                    <li>
                      <b>{"Plastic and Other Materials:"}</b>{" "}
                      {
                        "Perfect for labelling and selling items with QChat codes."
                      }
                    </li>
                    <li>
                      <b>{"Plastic and Other Materials:"}</b>{" "}
                      {
                        "Suitable for valuable items that require a durable QChat code."
                      }
                    </li>
                  </ul>
                  {
                    "You can find and explore these alternative forms in our online shop on the QChat website."
                  }
                </>
              ),
            },
            {
              q: "Can I use Qchat for business purposes?",
              a: (
                <>
                  {
                    "Absolutely! Qchat is suitable for both individuals and businesses. Stick Qchat codes on products, use it for secure business communication, or streamline lost-and-found processes. The possibilities are endless. Contact us on "
                  }
                  <a href="mailto:myinput@qchat.com.au">
                    {"myinput@qchat.com.au"}
                  </a>
                  {" for more details"}
                </>
              ),
            },
            {
              q: "How does the free trial work?",
              a: "Upon completing your registration, you'll enjoy a free trial period to experience the full benefits of Qchat. You can cancel at any time, and if you choose to continue, affordable subscription plans are available.",
            },
            {
              q: "Is Qchat available globally?",
              a: "Yes, Qchat is accessible worldwide. Stick Qchat codes on your belongings, and enjoy connections from anywhere in the world. It's a global solution for secure and convenient communication.",
            },
            {
              q: "Can I share personal details on Qchat?",
              a: "Qchat puts you in control. While personal details can be shared if you choose, it's not mandatory. You decide what information to share, when and with whom.",
            },
            {
              q: "How can I provide feedback or suggestions?",
              a: "We welcome your feedback! Feel free to reach out to our support team through the app. Your input helps us improve and enhance the Qchat experience for all users.",
            },
            {
              q: "How and where can I get my Qchat unique code?",
              a: (
                <>
                  {
                    "Obtaining your Qchat unique code is simple and tailored to your preferences. Upon subscribing or starting your free trial, you can access your unique QR code in various ways to suit different Qchat uses:"
                  }
                  <ul>
                    <li>
                      <b>{"Download and Save:"}</b>{" "}
                      {
                        "Easily download your Qchat code and choose to either print it or save it as a soft copy on your mobile or computer."
                      }
                    </li>
                    <li>
                      <b>{"Shop for Customization:"}</b>{" "}
                      {
                        "Explore our shop at www.qchat.com.au/shop  to order your Qchat code in different shapes and materials. Personalise it to match your style or specific needs."
                      }
                    </li>
                    <li>
                      <b>{"Special Requests:"}</b>{" "}
                      {
                        "Can't find what you're looking for? Have a suggestion for a new material or format? Contact us at "
                      }
                      <a href="mailto:myinput@qchat.com.au">
                        {"myinput@qchat.com.au"}
                      </a>
                      {
                        " and we'll work closely with you to bring your vision to life."
                      }
                    </li>
                  </ul>
                </>
              ),
            },
            {
              q: "What if I activated my free trial period and ordered my Qchat code from your shop, but it took a week or two for my order to arrive? Will you make up for this period for me?",
              a: "Absolutely! We understand the importance of a seamless experience. If you've activated your free trial and ordered a Qchat code from our shop, but experienced a delay, please reach out to us at hello@qchat.com.au  We'll promptly address the situation and ensure you don't miss out on any valuable time.",
            },
            {
              q: "I can see that you mentioned many uses for Qchat. What if I have another use that may need some modification or arrangements?",
              a: (
                <>
                  {
                    "We love hearing about unique ways our users want to implement Qchat! If you have a different use case or need specific modifications, please don't hesitate to contact us at "
                  }
                  <a href="mailto:myinput@qchat.com.au">
                    {"myinput@qchat.com.au"}
                  </a>
                  {
                    " Whether it's a suggestion or a modification request, we're here to collaborate with you and make your innovative ideas a reality. Your input is invaluable to us."
                  }
                </>
              ),
            },
          ].map((o, i) => {
            return (
              <div key={`faqi-${i}`} className="accordion-item">
                <h2 className="accordion-header" id={`FAQ-head-${i}`}>
                  <button
                    className="accordion-button collapsed"
                    type="button"
                    data-bs-toggle="collapse"
                    data-bs-target={`#FAQ-collapse-${i}`}
                    aria-expanded="false"
                    aria-controls={`FAQ-collapse-${i}`}
                  >
                    <span style={{ opacity: 0.7, fontSize: "0.7rem" }}>{`Q${
                      i + 1
                    }.`}</span>
                    <span style={{ padding: "0 10px" }}></span>
                    {o.q}
                  </button>
                </h2>
                <div
                  id={`FAQ-collapse-${i}`}
                  className="accordion-collapse collapse"
                  aria-labelledby={`FAQ-head-${i}`}
                  data-bs-parent="#accordionFlushExample"
                >
                  <div
                    className="accordion-body"
                    style={{ fontSize: "0.9rem" }}
                  >
                    {o.a}
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
}
